import Vue from 'vue';
import Router from 'vue-router';
import Dashboard from '@/components/Dashboard';
import NewEmployee from '@/components/NewEmployee';
import EditEmployee from '@/components/EditEmployee';
import ViewEmployee from '@/components/ViewEmployee';
import Login from '@/components/Login';
import Register from '@/components/Register';
import  firebase from 'firebase';



Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta:{
        requiresGuest:true
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta:{
        requiresGuest:true
      }    },
    {
      path: '/new',
      name: 'new-employee',
      component: NewEmployee,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/edit/:employee_id',
      name: 'edit-employee',
      component: EditEmployee,
      meta:{
        requiresAuth:true
      }
    },
    {
      path: '/:employee_id',
      name: 'view-employee',
      component: ViewEmployee,
      meta:{
        requiresAuth:true
      }
    },
    
  ]
});
// ADDED META TO PROTECT ANY PAGE OR INFOR THAT REQUIRES LOGIN
//belowcheccks thorugh the routes if it has requiresAut if nt it rediret to log in page or
// but if user is already logged in then it goes to the dashboard else just proceed to the choosen route

//Nav Guards
router.beforeEach((to, from, next) => {
  //check for requiredAuthGuard i.e if true the rout requires authentication
  if (to.matched.some(record => record.meta.requiresAuth)) {
    //Check if NOT logged in
    if (!firebase.auth().currentUser) {
      //if not logged in go to the user login page
      next({
        path: '/login',
        query:{
          redirect: to.fullPath
        }
      });      
    } else{
    //Proceed to the route by calling next
    next();
    }
  } else if (to.matched.some(record => record.meta.requiresGuest)) {
     //Check if logged in
     if (firebase.auth().currentUser) {
      //if logged in redirect to
      next({
        path: '/',
        query:{
          redirect: to.fullPath
        }
      });      
    } else{
    //Proceed to the route by calling next
    next();
    }
  } else{
    //proceed to the route
    next();
  }
});


// we can export router
export default router;



