<template>
    <div id="login">

        <div class="container">
      <div class="row">

          <div class="col s12 m8 offset-m2">
            <div class="login card-panel grey lighten-4 black-text center">
            <h3 id="title">Register</h3>
            <form>

            <div class="input-field">
                <i class="material-icons prefix">email</i>
              <input v-model="email" type="text" id="email">
              <label for="email" >email</label>
          </div>
          <div class="input-field">
                <i class="material-icons prefix">lock</i>
            <input v-model="password"  type="password" id="password">
            <label for="password">Password</label>
        </div>
        <a v-on:click="register" class="btn btn-large grey lighten-4 black-text" >Register</a>
        
        </form>

        </div>
      </div>
      </div>

    </div>
   
    </div>

</template>

<script>
import firebase from 'firebase';
export default{
        name: 'register',
        data (){
            return{
                email:'',
                password:'',
            };
        },
        methods: {
            register: function(e){
                firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
                .then(user => {
                    alert(`Account created for ${this.email}`);
                        this.$router.go({path:this.$router.path}); //use the.go function to reload the whole page and the navbar                    this.$router.push('/');  // GO BACK TO THE DASHBOARD
                },
                err=> {
                    alert(err.message);
                }
                );
                e.preventDefault();
            }
        }
       
    };
</script>




