<template>
    <div id="navbar">
        <nav>
            <div class="nav-wrapper green">
                <div class="container" >
                    <router-link to="/" class="brand-logo left">Employee Manager</router-link>
                <ul class="right">
                    <li v-if="isLoggedIn"><span class="email black-text">{{currentUser}}</span></li>
                    <!-- v-if to show logged in when user is logged in -->
                    <li v-if="isLoggedIn"><router-link to="/">Dashboard</router-link> </li>
                    <!-- v-id to not show login when use is logged in -->
                    <li v-if="!isLoggedIn"><router-link to="/login">Login</router-link> </li>
                    <li v-if="!isLoggedIn"><router-link to="/register">Register</router-link> </li>
                   <li v-if="isLoggedIn"><button v-on:click="logout" class="btn black">Logout</button></li>
                </ul>              
                </div>
            </div>
        </nav>
    </div>
</template>

<script>
import firebase from 'firebase';
export default {
    name: 'navbar',
    data(){
        return{
            isLoggedIn:false,
            currentUser:false
        }
    },
    // created after the elements have being loaded
    created() {
        if (firebase.auth().currentUser) {
            this.isLoggedIn = true;
            this.currentUser =firebase.auth().currentUser.email;
        }
    },
    methods: {
        logout: function(){
            firebase.auth().signOut()
            .then(()=> 
            this.$router.go({path:this.$router.path})  //use the.go function to reload the whole page and the navbar
            );
        }
    }
    
}



</script>

<style scoped>
    .email{
        padding-right: 10px;
}

/* the employee manager heading adjust on smaller screen */
@media only screen and (max-width: 640px) {
    .brand-logo{
        display: none;
    }
}
</style>



