<template>
    <div id="dashboard">
        <ul class="collection width-header">
            <li class="collection-header"><h4>Employees</h4>   
            </li>
            <!-- create a collection of employees and display  -->
            <li v-for="employee in employees"
            v-bind:key="employee.id" class="collection-item">
            <div class="chip">{{employee.department}}</div>{{employee.employee_id}} : {{employee.name}}
           
           <!-- creating the link to view employee being clicked-->
           <router-link class="secondary-content" v-bind:to="{name:
           'view-employee', params:{employee_id:employee.employee_id}}">
               <i class="fa fa-eye"></i>
            </router-link>
            </li>
        </ul>

        <div class="fix-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
    export default{
        name: 'dashboard',
        data (){
            return{
                employees: []

            }
        },
        created(){
            db.collection('Employees').orderBy('Department').get().then
            (querySnapshot => {
                querySnapshot.forEach(doc => {
                    //store the db info as a an object
                    const data = {
                        'id':doc.id,  // this is the firesbase id
                        'employee_id': doc.data().employee_id,
                        'name' : doc.data().name, //to get the employee name from firebase
                        'department': doc.data().Department,
                        'position':doc.data().Position,
                    }
                    this.employees.push(data)
                })
            })
        }
    }
</script>


