<template>
    <div id="view-employee">
        <ul class="collection width-header">
            <li class="collection-header"><h4>{{name}}</h4></li>
            <li class="collection-item">Employee ID#: {{employee_id}}</li>
            <li class="collection-item">Department: {{Department}}</li>
            <li class="collection-item">Position: {{Position}}</li>
        </ul>
        <!-- this btn goes to homepage -->
        <router-link to="/" class="btn grey">Back</router-link>

        <!-- Create a Delete btn -->
        <button @click="deleteEmployee"  class="btn red">Delete</button>
     <!--to edit employee  -->
     <div class="fix-action-btn">
            <router-link v-bind:to="{name:'edit-employee',
             params: {employee_id: employee_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
    export default{
        name: 'view-employee',
        data (){
            return{
                employee_id: null,
                name: null,
                Position: null,
                Department:null                
            }
        },
        // to execute before the page load get the employeeid compare and load teh employee
         beforeRouteEnter(to, from, next) {
            db.collection('Employees').where('employee_id',
            '==', to.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                        vm.employee_id = doc.data().employee_id,
                        vm.name = doc.data().name,
                        vm.Department = doc.data().Department,
                        vm.Position = doc.data().Position
                    })
                })
            })

        }, 
        //set to make to load the page before fetching the data
         watch: {
            '$route':'fetchData'
        }, 
        //create the fetch data method to collect the data and assign to employee object in data()
         methods: {
            fetchData(){
                db.collection('Employees').where('employee_id','==',
                this.$route.params.employee_id).get()
                .then(querySnapshot =>{
                    querySnapshot.forEach(doc =>{
                        // assign the data to object employee
                        this.employee_id =doc.data().employee_id,
                        this.name = doc.data().name,
                        this.Department = doc.data().Department,
                        this.Position = doc.data().Position
                    })
                })
            },
            // Delete an Employee
            deleteEmployee (){
                if(confirm('Are you sure')){
                    db.collection('Employees').where('employee_id','==',
                this.$route.params.employee_id).get()
                .then(querySnapshot =>{
                    querySnapshot.forEach(doc =>{
                        doc.ref.delete();
                        // after delete data go back to Hompage need to user router to redirect
                        this.$router.push('/')
                    })
                })
                }
            }
        }
    }
</script>


