<template>
    <div id="edit-Employee">
        <h3>Edit Employee</h3>
        <div class="row">
            <form @submit.prevent="updateEmployee" class="col s12">
                <div class="row">
                    <div class="input-field-col s12">
                        <input disabled type="text" v-model="employee_id" required>
                        
                    </div>
                </div>
                  <div class="row">
                    <div class="input-field-col s12">
                        <input type="text" v-model="name" required>
                        
                    </div>
                </div>
                  <div class="row">
                    <div class="input-field-col s12">
                        <input type="text" v-model="Department" required>
                        
                    </div>
                </div>
                  <div class="row">
                    <div class="input-field-col s12">
                        <input type="text" v-model="Position" required>
                        
                    </div>
                </div>
                <!-- btn to submit -->
                <button type="submit" class="btn">Submit</button>
                <!-- btn to go back -->
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
    export default{
        name: 'edit-Employee',
        data (){
            return{
                employee_id: null,
                name: null,
                Position: null,
                Department:null  
            }
        },
         // to execute before the page load get the employeeid compare and load teh employee
         beforeRouteEnter(to, from, next) {
            db.collection('Employees').where('employee_id',
            '==', to.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                        vm.employee_id = doc.data().employee_id,
                        vm.name = doc.data().name,
                        vm.Department = doc.data().Department,
                        vm.Position = doc.data().Position
                    })
                })
            })

        }, 
        //set to make to load the page before fetching the data
         watch: {
            '$route':'fetchData'
        }, 
        //create the fetch data method to collect the data and assign to employee object in data()
         methods: {
            fetchData(){
                db.collection('Employees').where('employee_id','==',
                this.$route.params.employee_id).get()
                .then(querySnapshot =>{
                    querySnapshot.forEach(doc =>{
                        // assign the data to object employee and add it to the form with v-model
                        this.employee_id =doc.data().employee_id,
                        this.name = doc.data().name,
                        this.Department = doc.data().Department,
                        this.Position = doc.data().Position
                    })
                })
            },
            updateEmployee () {
                  db.collection('Employees').where('employee_id','==',
                this.$route.params.employee_id).get()
                .then(querySnapshot =>{
                    querySnapshot.forEach(doc =>{
                        // assign the data to object employee and add it to the form with v-model
                       doc.ref.update({
                           employee_id: this.employee_id,
                           name: this.name,
                           Department: this.Department,
                           Position: this.Position
                       })
                    //    after update go bk to employee page
                       .then(() =>{
                           this.$router.push({name:'view-employee',
                            params:{employee_id:this.employee_id}})
                       })
                    })
                })

            }
         }
    }
</script>


